import React, { useEffect, useRef, useState } from 'react'
import { MapContainer, GeoJSON } from 'react-leaflet'
import * as L from "leaflet";
import "leaflet/dist/leaflet.css"
import 'leaflet-fullscreen/dist/Leaflet.fullscreen.js';
import 'leaflet-fullscreen/dist/leaflet.fullscreen.css';
import { Autocomplete, TextField } from "@mui/material";
import BaseMap from '../components/BaseMap';
import SearchBar from '../components/SearchBar';
import IndiaDistricts from '../assets/data/IndiaDistricts.json';
import AnnualFloodLegend from '../components/AnnualFloodLegend';
import IndiaStates from '../assets/data/IndiaStates.json';
import { Box } from "@mui/system";
import FiltererdJsonData from './FiltererdJsonData';
import ExportMapButton from './ExportMapButton';
import { mapCenter, maxBounds, setDragging, setInitialMapZoom } from '../helpers/functions';
import Districts_annual_flood_frac from "../assets/data/Districts_annual_flood_frac.json"

import States_annual_flood_frac from "../assets/data/States_annual_flood_frac.json"

import TimeSeriesCharts from '../components/TimeSeriesCharts';


const AnnualFloodPage = () => {
    const [selectedYear, setSelectedYear] = useState(null);
    const [selectedView, setSelectedView] = useState("")
    const [loading, setLoading] = useState(false);
    const [districtList, setDistrictList] = useState([]);
    const [filteredIndiaDistrict, setFilteredIndiaDistrict] = useState(null);
    const [selectedState, setSelectedState] = useState(null);
    const [selectedDistrict, setSelectedDistrict] = useState(null);
    const [showTimeseries, setShowTimeseries] = useState(false)
    const mapContainerRef = useRef(null);
    const [selectedFeatureID, setSelectedFeatureID] = useState(null);


    let selectedMapData;
    // let selectedChartData;
    let selectedGeoJSonData;

    if (selectedView === 'District Wise') {
        selectedMapData = Districts_annual_flood_frac;
        // selectedChartData = Districts_annual_flood_area;
        selectedGeoJSonData = IndiaDistricts
    } else if (selectedView === 'State Wise') {
        selectedMapData = States_annual_flood_frac;
        // selectedChartData = States_annual_flood_area;
        selectedGeoJSonData = IndiaStates
    } else {
        selectedMapData = null
        // selectedChartData = null
        selectedGeoJSonData = null
    }




    const handleShowTimeseries = () => {
        setShowTimeseries(!showTimeseries)
    }

    const handleViewSelection = (event, value) => {
        setSelectedView(value);
        setSelectedFeatureID(null)
        setShowTimeseries(false)
        setSelectedState(null)
        setSelectedDistrict(null)
        setFilteredIndiaDistrict(null)
        setSelectedYear(null)
        
    };

    const handleStateSelect = (event, value) => {
        let items = IndiaDistricts.features.filter((item) => item.properties.STATE === value);
        items = [...new Set(items.map((item) => item))];
        items.sort();

        setDistrictList(items);
        setSelectedState(value)

        let filteredStateFeatures = selectedGeoJSonData.features.filter((feature) => feature.properties.STATE === value);


        setFilteredIndiaDistrict({
            type: "FeatureCollection",
            features: filteredStateFeatures,
        });

        if (selectedView === 'State Wise') {
            setSelectedFeatureID(filteredStateFeatures[0].properties.ID)
        }

    
    };




    const handleDistrictSelect = (event, value) => {
        setSelectedDistrict(value)

        let filteredDistrictFeatures = IndiaDistricts.features.filter((feature) => feature.properties.DISTRICT === value && feature.properties.STATE === selectedState);

        setFilteredIndiaDistrict({
            type: "FeatureCollection",
            features: filteredDistrictFeatures,
        });
        if (selectedView === 'District Wise') {
            setSelectedFeatureID(filteredDistrictFeatures[0].properties.ID)
        }


       


    };



    const handleYearChange = (event, value) => {
        setSelectedYear(value);
    };


   




    function DistrictOnEachfeature(feature, layer) {
        layer.on('mouseover', function () {
            const DataItem = selectedMapData.find(item => item.ID === feature.properties.ID);
            if (selectedYear && feature.properties && feature.properties.ID) {
                let popupContent;
                if (selectedView === 'District Wise') {
                    popupContent = `
          <div>
            ID: ${feature.properties.ID}<br/>
            DISTRICT: ${feature.properties.DISTRICT}<br/>
            STATE: ${feature.properties.STATE}<br/>
            Flooded Area (Fraction): ${DataItem ? DataItem[selectedYear] : null}<br/>
          </div>
        `;
                } else if (selectedView === 'State Wise') {
                    popupContent = `
          <div>
            ID: ${feature.properties.ID}<br/>
            STATE: ${feature.properties.STATE}<br/>
            Flooded Area (Fraction): ${DataItem ? DataItem[selectedYear] : null}<br/>
          </div>
        `;
                }
                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on('mouseout', function () {
            layer.closeTooltip();
        });
    }



    const DistrictDensity = (density => {
        return density > 7 ? '#003069'
            : density > 5 ? '#005199'
                : density > 3 ? '#0171B2'
                    : density > 1 ? '#2D92C3'
                        : density > 0.8 ? '#60AED4'
                            : density > 0.6 ? '#97CAE1'
                                : density > 0.4 ? '#C4DBEE'
                                    : density > 0.2 ? '#DCEBF6'
                                        : '#F7FBFF';
    })





    const DistrictStyle = (feature => {

        const getDensityFromSummerData = (id) => {
            const DataItem = selectedMapData.find(item => item.ID === id);
            return DataItem ? DataItem[selectedYear] : null;

        };

        const density = getDensityFromSummerData(feature.properties.ID);
        return ({
            fillColor: DistrictDensity(density),
            weight: 1,
            opacity: 1,
            color: 'black',
            dashArray: '2',
            fillOpacity: 1
        });



    });




    return (
        <div className='dasboard_page_container'>

            <div className='main_dashboard'>
                <div className='left_panel'>

                    <div className='border border-secondary p-2 mb-2'>
                        <Autocomplete
                            value={selectedView}
                            onChange={handleViewSelection}
                            id="session"
                            options={[
                                "State Wise",
                                "District Wise",
                            ]}
                            renderInput={(params) => (
                                <TextField {...params} className="form-select mb-3" label="Select Dataset" />
                            )}
                        />


                    </div>

                    <div className='border border-secondary p-2 mb-2'>
                        <Autocomplete
                            onChange={(event, value) => handleYearChange(event, value)}
                            id="year"
                            value={selectedYear}
                            options={Array.from({ length: 2020 - 1900 }, (_, index) => `${1901 + index}`)}
                            noOptionsText="No Available Data"
                            renderOption={(props, year) => (
                                <Box component="li" {...props} key={year}
                                    sx={{
                                        fontSize: "14px",
                                    }}>
                                    {year}
                                </Box>
                            )}
                            renderInput={(params) => (
                                <TextField
                                    {...params}
                                    label="Select Year"
                                    className="form-select"
                                    aria-label="Default select example"
                                />
                            )}
                        />

                    </div>



                    <div className='border border-secondary p-2 mb-2'>
                        <label className="form-label">Filter Data:</label>

                        <Autocomplete
                            style={{ marginBottom: "20px" }}
                            onChange={(event, value) => handleStateSelect(event, value)}
                            id="state"
                            value={selectedState}
                            getOptionLabel={(state) => `${state}`}
                            options={[...new Set(IndiaStates.features.map((item) => item.properties.STATE))]}
                            isOptionEqualToValue={(option, value) => option.Name === value.Name}
                            noOptionsText={"No Available Data"}
                            disabled={!selectedView}
                            renderOption={(props, state) => (
                                <Box component="li" {...props} key={state}
                                    sx={{
                                        fontSize: "14px",
                                    }}>
                                    {state}
                                </Box>
                            )}
                            renderInput={(params) => <TextField {...params} label="Search State" />}
                        />

                        {selectedView === "District Wise" && (
                            <Autocomplete
                            value={selectedDistrict}
                                style={{ marginBottom: "20px" }}
                                onChange={(event, value) => handleDistrictSelect(event, value)}
                                id="district"
                                getOptionLabel={(district) => `${district}`}
                                options={[...new Set(districtList.map((item) => item.properties.DISTRICT))]}
                                isOptionEqualToValue={(option, value) => option.Name === value.Name}
                                noOptionsText={"No Available Data"}
                                disabled={districtList.length === 0}
                                renderOption={(props, district) => (
                                    <Box component="li" {...props} key={district}
                                        sx={{
                                            fontSize: "14px",
                                        }}>
                                        {district}
                                    </Box>
                                )}
                                renderInput={(params) => <TextField {...params}

                                    label="Search District" />}
                            />
                        )}


                    </div>

                    <div className='panel_button'>
                        <button type='button' disabled={!selectedView || !selectedYear || !selectedFeatureID} onClick={handleShowTimeseries}>
                            {showTimeseries ? "Hide Timeseries" : "Show Timeseries"}
                        </button>
                    </div>




                </div>



                <div className='right_panel' ref={mapContainerRef}>

                    <MapContainer
                        fullscreenControl={true}

                        center={mapCenter}
                        style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
                        zoom={setInitialMapZoom()}
                        maxBounds={maxBounds}
                        // maxZoom={8}
                        minZoom={setInitialMapZoom() - 1}
                        keyboard={false}
                        dragging={setDragging()}
                        // attributionControl={false}
                        // scrollWheelZoom={false}
                        doubleClickZoom={false}
                    >
                        <SearchBar />
                        {/* <ExportMapButton mapContainerRef={mapContainerRef} selectedYear={selectedYear} selectedSession={selectedView} /> */}


                        {showTimeseries && selectedFeatureID  &&  (
                            <div className='time_series_container'>
                                <TimeSeriesCharts 
                                selectedView={selectedView}
                                handleShowTimeseries={handleShowTimeseries} 
                                // selectedChartData={selectedChartData} 
                                selectedFeatureID={selectedFeatureID}
                                selectedDistrict={selectedDistrict}
                                selectedState={selectedState}
                                selectedYear={selectedYear}
                                 />
                            </div>
                        )}


                        {selectedView && selectedYear && selectedMapData && (
                            <>

                                {filteredIndiaDistrict && (
                                    <FiltererdJsonData
                                        selectedState={selectedState}
                                        selectedDistrict={selectedDistrict}
                                        // DistrictStyle={DistrictStyle}
                                        filteredIndiaDistrict={filteredIndiaDistrict}
                                    // DistrictOnEachfeature={DistrictOnEachfeature}
                                    />


                                )}

                                <GeoJSON
                                    key={`${selectedView}+${selectedYear}`}
                                    style={DistrictStyle}
                                    data={selectedGeoJSonData.features}
                                    onEachFeature={DistrictOnEachfeature}
                                />


                                <div className="legend-panel-desktop">
                                    <AnnualFloodLegend year={selectedYear} />
                                </div>
                            </>


                        )}


                        <BaseMap />

                    </MapContainer>



                    {selectedYear && (
                        <div className="legend-panel-mobile">
                            <AnnualFloodLegend year={selectedYear} />
                        </div>
                    )}



                    {loading && (
                        <div className='map_layer_loader_container_desktop'>
                            <div className="map_loader_container">
                                <span className="map_loader"></span>
                            </div>

                        </div>

                    )}

                </div>


                {loading && (
                    <div className='map_layer_loader_container_mobile'>
                        <div className="map_loader_container">
                            <span className="map_loader"></span>
                        </div>

                    </div>

                )}


            </div>

        </div>
    )
}

export default AnnualFloodPage