import React from 'react';
import Chart from 'react-apexcharts';
import { IoMdClose } from "react-icons/io";
import States_annual_flood_area from "../assets/data/States_annual_flood_area.json"
import Districts_annual_flood_area from "../assets/data/Districts_annual_flood_area.json"

const TimeSeriesCharts = ({ selectedView, handleShowTimeseries, selectedFeatureID, selectedState, selectedDistrict, selectedYear }) => {


    let selectedChartData;
    if (selectedView === 'District Wise') {
        selectedChartData = Districts_annual_flood_area;
    } else if (selectedView === 'State Wise') {
        selectedChartData = States_annual_flood_area;
    } else {
        selectedChartData = null
    }


    const chartData = selectedChartData && selectedChartData.find(item => item.ID === selectedFeatureID);

    // Extract years and corresponding flood areas from chart data
    const years = chartData && Object.keys(chartData).filter(key => key !== 'ID').map(year => parseInt(year));
    const floodAreas = chartData && Object.values(chartData).filter(val => typeof val === 'number').map(val => val.toFixed(2));

    // Prepare the series data for the chart
    const seriesData = [{
        name: 'Flooded Area (km²)',
        data: floodAreas
    }];





    return (
        <>
            <div className='timeseries_heading'>

                {selectedView === 'District Wise' ? (
                    <h4>
                        Annual Flooded Area Timeseries of {selectedDistrict}, {selectedState} in {selectedYear}

                    </h4>
                ) : selectedView === 'State Wise' ? (
                    <h4>
                        Annual Flooded Area Timeseries of {selectedState}  in {selectedYear}

                    </h4>
                ) : (
                    null
                )}

                <button className='chart_close_btn' onClick={handleShowTimeseries}><IoMdClose /></button>
            </div>


            <Chart
                options={{
                    chart: {
                        type: 'line',
                        stacked: false,
                        zoom: {
                            type: 'x',
                            enabled: true,
                            autoScaleYaxis: true
                        },
                        toolbar: {
                            autoSelected: 'zoom'
                        },
                        dataLabels: {
                            enabled: false
                        },


                    },
                    xaxis: {
                        categories: years,
                        title: {
                            text: 'Time',
                            offsetX: 10
                        }
                    },
                    yaxis: {
                        title: {
                            text: 'Flooded Area (km²)',
                            offsetY: 10
                        }
                    },
                    responsive: [{
                        breakpoint: 480,
                        options: {
                            legend: {
                                position: 'bottom',
                                offsetX: -10,
                                offsetY: 0
                            }
                        }
                    }]
                }}
                series={seriesData}
                type="line"
                width="100%"
                height="100%"
            />

        </>

    );
};

export default TimeSeriesCharts;
